export default function howManyProducts() {
  let stringProduct = "";
  if (this.$store.state.cartProducts.length === 0) {
    stringProduct = "корзина пустая";
  } else if (this.$store.state.cartProducts.length === 1) {
    stringProduct = "товар";
  } else if (this.$store.state.cartProducts.length <= 4) {
    stringProduct = "товара";
  } else {
    stringProduct = "товаров";
  }
  return stringProduct;
}