<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"
        >{{ $store.state.cartProducts.length }} {{ howManyProducts }}</span
      >
    </div>
    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div class="load" v-if="cartLoading">
            <p class="load__descr">загрузка корзины ...</p>
            <span class="loader"></span>
          </div>
          <ul v-else class="cart__list">
            <CartItem
              v-for="item of products"
              :key="item.productId"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link
            class="cart__button button button--primery"
            :to="{ name: 'order' }"
            tag="button"
            type="submit"
            v-if="$store.state.cartProductsData.length"
          >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem.vue";
export default {
  components: { CartItem },
  filters: { numberFormat },
  computed: {
    ...mapGetters({ totalPrice: "cartTotalPrice" }),
    products() {
      return this.$store.getters.cartDetailProducts;
    },
    cartLoading() {
      return this.$store.state.cartLoading;
    },
    howManyProducts() {
      let stringProduct = "";
      if (this.$store.getters.cartDetailProducts.length === 0) {
        stringProduct = "корзина пустая";
      } else if (this.$store.getters.cartDetailProducts.length === 1) {
        stringProduct = "товар";
      } else if (this.$store.getters.cartDetailProducts.length <= 4) {
        stringProduct = "товара";
      } else {
        stringProduct = "товаров";
      }
      return stringProduct;
    },
  },
};
</script>