<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{
        name: 'product',
        params: { id: product.id, color: chosenProductColor },
      }"
    >
      <img :src="product.img" alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numberFormat }} ₽</span>

    <ProductColor
      @set-color="(color) => $emit('set-color', color, productId)"
      :colors="product.colors"
      :chosen-product-color="chosenProductColor"
    />
  </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import ProductColor from "@/components/ProductColor.vue";
export default {
  components: { ProductColor },
  data() {
    return {
      productId: this.product.id,
      color: null,
    };
  },
  props: [
    "product",
    {
      chosenProductColor: {
        require: false,
      },
    },
  ],
  methods: {
    gotoPage,
  },
  created() {
    if (this.chosenProductColor) this.color = this.chosenProductColor.color;
  },
  filters: {
    numberFormat,
  },
};
</script>