<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="validateForm"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              title="ФИО"
              :error="formError.name"
              :front="front.name"
              placeholder="Введите ваше полное имя"
            />
            <BaseFormText
              v-model="formData.address"
              title="Адрес доставки"
              :error="formError.address"
              :front="front.address"
              placeholder="Введите ваш адрес"
            />
            <BaseFormText
              v-model="formData.phone"
              title="Телефон"
              :error="formError.phone"
              :front="front.phone"
              placeholder="Введите ваш телефон"
            />
            <BaseFormText
              v-model="formData.email"
              title="Email"
              :error="formError.email"
              :front="front.email"
              placeholder="Введи ваш Email"
            />
            <BaseFormTextArea
              v-model="formData.comment"
              title="Комментарий к заказу"
              :error="formError.comment"
              :front="front.comment"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in products"
              :key="item.productId"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.product.price * item.amount) | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              Доставка: <b>{{ productDeliveryPrice | numberFormat }} ₽</b>
            </p>
            <p>
              Итого: <b>{{ $store.getters.cartTotalItems }}</b>
              {{ howManyProducts }} на сумму
              <b>{{ totalPriceDelivery | numberFormat }} ₽</b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextArea from "@/components/BaseFormTextArea";
import howManyProducts from "@/helpers/howManyProducts";
import { mapGetters } from "vuex";
import { mapState } from "vuex";
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  name: "OrderPage",
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
      sendingBasket: false,
      errors: [],
      front: {
        name: "",
        address: "",
        phone: "",
        email: "",
      },
    };
  },
  components: { BaseFormText, BaseFormTextArea },
  computed: {
    howManyProducts,
    ...mapGetters({ totalPrice: "cartTotalPrice" }),
    ...mapState({ deliveryPrice: "productDeliveryPrice" }),
    products() {
      return this.$store.getters.cartDetailProducts;
    },
    totalPriceDelivery() {
      return this.totalPrice + this.deliveryPrice;
    },
    productDeliveryPrice() {
      return this.deliveryPrice;
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    validEmail: function (email) {
      let pattern =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return pattern.test(email);
    },
    validPhone: function (phone) {
      let pattern = /^\+\d{11}$/;
      return pattern.test(phone);
    },
    validateForm() {
      this.errors = [];
      this.front.name = "";
      this.front.address = "";
      this.front.phone = "";
      this.front.email = "";
      if (!this.formData.name) {
        this.errors.push("Имя");
        this.front.name = "Введите свое имя";
      }
      if (!this.formData.address) {
        this.errors.push("Адрес");
        this.front.address = "Введите адрес доставки";
      }
      if (!this.formData.phone) {
        this.errors.push("Телефон");
        this.front.phone = "Вы забыли указать свой номер телефона";
      } else if (!this.validPhone(this.formData.phone)) {
        this.errors.push("Телефон");
        this.front.phone = "некорректный номер телефона";
      }
      if (!this.formData.email) {
        this.errors.push("Емейл");
        this.front.email = "Заполните поле Email";
      } else if (!this.validEmail(this.formData.email)) {
        this.errors.push("Емейл");
        this.front.email = "некорректный адрес электронной почты";
      }
      if (!this.errors.length) {
        this.order();
      }
    },
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.sendingBasket = true;

      axios
        .post(
          API_BASE_URL + "/api/orders",
          {
            ...this.formData,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit("resetCart");
          this.sendingBasket = false;
          this.$store.commit("updateOrderInfo", response.data);
          this.$router.push({
            name: "orderInfo",
            params: { id: response.data.id },
          });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.sendingBasket = false;
        });
    },
  },
};
</script>
