<template>
  <ul class="catalog__list">
    <ProductItem
      :product="product"
      v-for="product in products"
      :key="product.id"
      :chosen-product-color="returnColor(product.id)"
      @set-color="(color, productId) => saveColor(color, productId)"
    />
  </ul>
</template>

<script>
import ProductItem from "./ProductItem";

export default {
  components: { ProductItem },
  props: ["products"],
  data() {
    return {
      chosenColor: [],
    };
  },
  methods: {
    saveColor(color, productId) {
      let productColors = [];
      productColors = this.chosenColor.filter(
        (product) => product.id !== productId
      );
      productColors.push({
        id: productId,
        color,
      });
      this.chosenColor = productColors;
    },
    returnColor(productId) {
      return this.chosenColor.find((product) => product.id === productId);
    },
  },
};
</script>